import axios from 'axios'

import { Notification } from 'element-ui'

export function request() {
    const instance = axios.create({
        baseURL: window.location.origin,
        timeout: 5000
    })


    //请求返回拦截，把数据返回到页面之前做些什么...
    instance.interceptors.response.use(response => {
        // console.log("调用了响应 成功拦截器", response)
        //特殊错误处理，状态为10时为登录超时
        if (response.data.errcode != 2000) {
            Notification.error({ "title": '操作失败', 'message':  response.data.errmsg})

        }
        return response
        
    }, err => {
        console.log("调用了响应 失败拦截器", err)
        Notification.error({ "title": '请求错误', 'message': err })
    })


    // 请求拦截器
    instance.interceptors.request.use(
        config => {
            console.log('config', config)
            return config;
        },
        error => {
            // 对请求错误做些什么，自己定义
            // Message({                  //使用element-ui的message进行信息提示
            //     showClose: true,
            //     message: error,
            //     type: "warning"
            // });
            console.log('error', error)
            return Promise.reject(error);
        }
    )

    //     //......

    // }, function (error) {
    //     return Promise.reject(error);
    // });

    return instance
}