<template>
  <el-container>
    <el-main>
      <p @click="initData()">是否连接了: {{isConnected}}</p>

      <div class="demo-input-suffix">
        <el-input
          size="large"
          placeholder="mqtt.com:1883"
          prefix-icon="el-icon-s-platform"
          v-model="mqttBrokerURL"
        ></el-input>
      </div>
      <div class="demo-input-suffix">
        <el-input size="large" placeholder="用户名" prefix-icon="el-icon-user" v-model="mqttUserName"></el-input>
      </div>
      <div class="demo-input-suffix">
        <el-input size="large" placeholder="密码" prefix-icon="el-icon-lock" v-model="mqttPassword"></el-input>
      </div>
      <el-button
        type="primary"
        style="margin-bottom: 50px"
        @click="connectMqtt()"
        :loading="connecting"
      >连接</el-button>
      <el-button
        type="danger"
        style="margin-bottom: 50px"
        @click="disconnect()"
        :autosize="true"
        v-show="isConnected"
      >断开</el-button>

      <div class="demo-input-suffix">
        <el-input size="large" placeholder="Topic" v-model="topic"></el-input>
      </div>

      <div class="demo-input-suffix">
        <el-input type="textarea" :rows="2" placeholder="要发送的消息" v-model="message"></el-input>
      </div>

      <el-button type="primary" style="margin-bottom: 50px" @click="sendMessage()">发送</el-button>

      <div class="demo-input-suffix">
        <el-input size="large" placeholder="Topic" v-model="subTopic"></el-input>
      </div>
      <el-button type="primary" style="margin-bottom: 50px" @click="subscribe()">订阅</el-button>

      <div class="demo-input-suffix">
        <el-input
          type="textarea"
          :rows="2"
          placeholder="收到的消息"
          :autosize="true"
          v-model="recvMessage"
        ></el-input>
      </div>
      <el-button type="primary" style="margin-bottom: 50px" @click="getRecvMessage()">刷新</el-button>
      <el-button
        type="danger"
        style="margin-bottom: 50px"
        @click="deleteMessage()"
      >{{ stopRecv?"继续":"暂停" }}</el-button>
    </el-main>
  </el-container>
</template>

<script>
import { postMqtt } from "../network/home";
import { request } from "../network/request";
import { Notification } from "element-ui";
import { WEBSOCKET_URL } from "../common/index"

export default {
  name: "HelloWorld",
  created() {
    this.initData();
    this.initWebSocket();
  },
  data: function() {
    return {
      mqttBrokerURL: "mqtt.com:1883",
      mqttUserName: "admin",
      mqttPassword: "admin",
      topic: "/testtopic",
      message: '{"msg": "hello world!"}',
      isConnected: false,
      connecting: false,
      subTopic: "/testtopic",
      recvMessage: "",
      stopRecv: false,

      wsuri: WEBSOCKET_URL,
      socket: null
    };
  },

  destroyed() {
    this.socket.close();
  },
  methods: {
    deleteMessage() {
      this.stopRecv = !this.stopRecv;
    },
    getRecvMessage() {
      request()
        .get("/mqtt/message")
        .then(res => {
          console.log(res);
          this.recvMessage = res.data.data;
        });
    },
    subscribe() {
      request()
        .post("/mqtt/subscription", { topic: this.subTopic })
        .then(res => {
          if (res.data.errcode == 2000) {
            Notification.success("订阅成功");
          }
        });
    },
    connectMqtt() {
      this.connecting = true;
      postMqtt(this.mqttBrokerURL, this.mqttUserName, this.mqttPassword)
        .then(res => {
          console.log("connectMqtt", res);
          if (res.data.errcode == 2000) {
            this.isConnected = true;
          }
        })
        .finally(() => {
          this.connecting = false;
        });
    },

    disconnect() {
      request()
        .delete("/mqtt")
        .then(res => {
          console.log(res);
          this.isConnected = false;
        });
    },
    sendMessage() {
      request().post("/mqtt/message", {
        topic: this.topic,
        message: this.message
      });
    },
    initData() {
      request()
        .get("/mqtt/status")
        .then(res => {
          console.log(res);
          this.isConnected = res.data.data["is_connected"];
        })
        .catch(err => {
          console.log("/status error", err);
        });
    },

    // websocket 相关
    reconnect() {
      console.log("尝试重连");
      if (this.lockReconnect || this.maxReconnect <= 0) {
        return;
      }
      setTimeout(() => {
        // this.maxReconnect-- // 不做限制 连不上一直重连
        this.initWebSocket();
      }, 60 * 1000);
    },

    initWebSocket() {
      try {
        if ("WebSocket" in window) {
          this.socket = new WebSocket(this.wsuri);
        } else {
          console.log("您的浏览器不支持websocket");
        }
        this.socket.onopen = this.websocketonopen;
        this.socket.onerror = this.websocketonerror;
        this.socket.onmessage = this.websocketonmessage;
        this.socket.onclose = this.websocketclose;
      } catch (e) {
        this.reconnect();
      }
    },

    websocketonopen() {
      console.log("WebSocket连接成功", this.socket.readyState);

      // this.socket.send('发送数据')
      // this.websocketsend();
    },
    websocketonerror(e) {
      console.log("WebSocket连接发生错误", e);
      this.reconnect();
    },
    websocketonmessage(e) {
      // console.log(e)
      // let data = JSON.parse(e.data);
      console.log("websocket数据", e.data);
      // 消息获取成功，重置心跳
      if (!this.stopRecv) {
        if (this.recvMessage.length >= 1000 ) {
          this.recvMessage = ''
        }
        this.recvMessage += e.data;
        this.recvMessage += "\n\n";
      }
    },
    websocketclose(e) {
      console.log("connection closed (" + e.code + ")");
      this.reconnect();
    },
    websocketsend(message) {
      console.log(message);
      this.socket.send(message);
    }
  },
  computed: {}
};
</script>

